import React,{Component} from 'react';

class SearchBar extends Component {

    constructor(props){
        super(props);
        this.state = { term : ''};
    }

    render(){
        return (
        <div className="search-bar col-lg-10">
        <input className="form-control form-rounded" placeholder="Enter something to search..."
        value={this.state.term}
        onChange={event => this.onInputChange(event.target.value)} />
        </div>
        )
    }
onInputChange(term){
    this.setState({term});
    this.props.onSearchTermChange(term);
}

}

export default SearchBar;


